<template>
  <v-dialog v-model="isOpen" max-width="500px" persistent>
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="networkName" disabled class="centered-input" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_name" label="device name" required :rules="[() => !onCheckExisting(editedItem, 'device_name') || 'Name Existed', rules.required]" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.device_enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.type_mstp" label="type mstp"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.supports_rpm" label="supports rpm"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.supports_wpm" label="supports wpm"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_ip" label="device ip" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_port" label="port" type="number" min="0" @input="numberChange('device_port')" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_mask" label="mask" type="number" min="0" @input="numberChange('device_mask')" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_mac" label="mac" type="number" min="0" @input="numberChange('device_mac')" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.device_object_id" label="id" type="number" min="0" @input="numberChange('device_object_id')" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.network_number" label="network number" type="number" min="0" @input="numberChange('network_number')" />
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.ethernet_mac_address" label="ethernet mac address" />
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text @click="()=>{onClose(); $refs.form.resetValidation()}">Cancel</v-btn>
          <v-btn color="primary" text @click="submit" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
import { RULES } from './bacnetMasterConstant';

export default {
  props: ['on-close', 'is-open', 'on-save', 'form-title', 'network-name', 'edited-item', 'on-check-existing'],
  data: () => ({
    rules: {...RULES},
    valid: false,
  }),
  methods:{
    submit() {
      if (this.$refs.form.validate()) {
        this.onSave();
      }
    },
    numberChange(fieldName){
      this.editedItem[fieldName] = this.editedItem[fieldName] ? Number(this.editedItem[fieldName]) : 0;
    },
  }
};
</script>


