<template>
  <v-app>
    <right-sidebar :menuItems="networksData" serverRoute="bacnet-master"></right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '' : 'nubeGreen'}`">
          <breadcrumbs-direction :items="dirs" />
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="deviceHeaders"
              :items="dataTable"
              class="elevation-1 text-left"
              item-key="device_uuid"
              show-select
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              sort-by="device_uuid"
              group-by="category"
              show-group-by
              dense
              :custom-filter="filterCriteria"
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>BACnet Devices View</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="onClickRefresh"
                      >Refresh<v-icon x-small right>mdi-refresh</v-icon></v-btn
                    >
                    <v-btn
                      x-small
                      class="mr-1 btn-height-22 white--text"
                      color="error"
                      @click="clickDeleteMulti"
                      :disabled="selected.length === 0"
                      >Delete<v-icon x-small right>mdi-delete</v-icon></v-btn
                    >
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                    @input="onSearchChange"
                  ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:[`group.header`]="{ group, headers, toggle, isOpen, items }">
                <td :colspan="headers.length">
                  <div class="row" style="align-items: center">
                    <v-btn @click="toggle" x-small icon :ref="group">
                      <v-icon>{{ isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right' }}</v-icon> </v-btn
                    >&nbsp;&nbsp;&nbsp;&nbsp;
                    <v-checkbox
                      v-bind:value="
                        selected.filter(d => d.network_uuid === items[0].network_uuid).length == items.length
                      "
                      @change="check($event, items)"
                    ></v-checkbox>
                    <router-link
                      :to="`/bacnet-master/networks/${items[0].network_uuid}/devices`"
                      class="font-weight-bold text-style"
                    >
                      {{ group }}
                    </router-link>
                  </div>
                </td>
              </template>

              <template v-slot:[`item.device_enable`]="{ item }">
                <v-checkbox v-model="item.device_enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.type_mstp`]="{ item }">
                <v-checkbox v-model="item.type_mstp" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.supports_wpm`]="{ item }">
                <v-checkbox v-model="item.supports_wpm" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.supports_rpm`]="{ item }">
                <v-checkbox v-model="item.supports_rpm" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:[`item.device_ip`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.device_ip"
                  @save="updateDevice(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.device_ip }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.device_ip" single-line></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.device_mask`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.device_mask"
                  @save="updateDevice(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.device_mask }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.device_mask"
                      single-line
                      type="number"
                      @input="
                        val => {
                          val ? (item.device_mask = val) : (item.device_mask = 0);
                        }
                      "
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.device_port`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.device_port"
                  @save="updateDevice(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.device_port }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.device_port"
                      single-line
                      type="number"
                      @input="
                        val => {
                          val ? (item.device_port = val) : (item.device_port = 0);
                        }
                      "
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.device_mac`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.device_mac"
                  @save="updateDevice(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.device_mac }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.device_mac"
                      single-line
                      type="number"
                      @input="
                        val => {
                          val ? (item.device_mac = val) : (item.device_mac = 0);
                        }
                      "
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.device_object_id`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.device_object_id"
                  @save="updateDevice(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.device_object_id }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.device_object_id"
                      single-line
                      type="number"
                      @input="
                        val => {
                          val ? (item.device_object_id = val) : (item.device_object_id = 0);
                        }
                      "
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:[`item.network_number`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.network_number"
                  @save="updateDevice(item)"
                  @cancel="cancelEdit"
                  @open="openEdit"
                  @close="closeEdit"
                  large
                >
                  <b>{{ item.network_number }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.network_number"
                      single-line
                      type="number"
                      @input="
                        val => {
                          val ? (item.network_number = val) : (item.network_number = 0);
                        }
                      "
                    ></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:[`item.actions`]="{ item }">
                <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
              </template>

              <!-- header -->
              <template v-slot:[`header.device_enable`]>
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('device_enable'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.device_ip`]>
                {{ toUpperTextKey('ip') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('device_ip'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.device_mask`]>
                {{ toUpperTextKey('mask') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('device_mask'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.device_port`]>
                {{ toUpperTextKey('port') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('device_port'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.device_mac`]>
                {{ toUpperTextKey('mac') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('device_mac'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.network_number`]>
                {{ toUpperTextKey('network_number') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('network_number'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.type_mstp`]>
                {{ toUpperTextKey('type_mstp') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('type_mstp'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.supports_rpm`]>
                {{ toUpperTextKey('supports_rpm') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('supports_rpm'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:[`header.supports_wpm`]>
                {{ toUpperTextKey('supports_wpm') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('supports_wpm'), $event.stopPropagation()" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>
              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <system-status-table server="bacnet-master"></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!--Edit dialog-->
    <bm-device-form-dialog
      :is-open="dialog"
      :on-close="close"
      :on-save="() => updateDevice(editedItem)"
      :form-title="'Edit Device'"
      :network-name="network.network_name"
      :edited-item="editedItem"
      :on-check-existing="isExistingvalue"
    ></bm-device-form-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <bm-mass-edit-dialog
      :is-open="dialogMassEdit"
      :on-close="() => (dialogMassEdit = false)"
      :edit-all="editAll"
      :edit-field="editField"
      :on-save="saveMassEdit"
    ></bm-mass-edit-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import BreadcrumbsDirection from '../Common/BreadcrumbsDirection.vue';
import BmMassEditDialog from './BmMassEditDialog';
import BmDeviceFormDialog from './BmDeviceFormDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { DEVICE_HEADERS, RULES, DEVICE_EDIT_ALL_DEFAULT } from './bacnetMasterConstant';
import { toUpperTextKey } from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    BmMassEditDialog,
    BmDeviceFormDialog,
    SystemStatusTable,
    BreadcrumbsDirection,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    action: '',
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    deviceHeaders: [...DEVICE_HEADERS],
    currentItem: {},
    editedItem: {},
    rules: { ...RULES },
    search: '',
    searchItems: [],
    dialogMassEdit: false,
    editAll: {},
    editField: '',
    intervalId: null,
    devicesOfNW: [],
    dataTable: [],
    dirs: [
      { text: 'NETWORKS', link: '/bacnet-master/networks', disabled: false },
      { text: 'DEVICES VIEW', disabled: true },
    ],
    networks: [],
    network: {},
    tableProps: { ...DATA_TABLE_PROPS },
    tab: 0,
    loadingData: false,
  }),
  computed: {
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
    networksData() {
      return this.$store.state.bmNetworks;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$route.params.id': function (val, oldVal) {
      if (val != oldVal) {
        this.getAllDevices();
      }
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getAllDevices();

    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getAllDevices();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getAllDevices() {
      this.loadingData = true;
      axios.get(`bacnet_master/networks`).then(data => {
        this.networks = data.networks;
        this.getDataTable(this.networks);
        this.loadingData = false;
      });
    },
    getDataTable(networks) {
      this.dataTable = [];
      networks.forEach(network => {
        if (network.devices.length > 0) {
          const devicesPreNW = network.devices.map(device => {
            return { ...device, category: network.network_name };
          });
          this.dataTable = this.dataTable.concat(devicesPreNW);
        }
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable.findIndex(d => d.device_uuid == item.device_uuid);
      this.devicesOfNW = this.dataTable.filter(device => device.network_uuid === item.network_uuid);
      this.editedItem = Object.assign({}, item);
      this.network = this.networks.find(n => n.network_uuid === item.network_uuid);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable.indexOf(this.currentItem);
      this.dataTable.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/device/${this.currentItem.device_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(item, fieldName) {
      if (!this.devicesOfNW) return false;
      const value = item[fieldName];
      let devices = _cloneDeep(this.devicesOfNW);
      const index = devices.findIndex(d => d.device_uuid == item.device_uuid);
      devices.splice(index, 1);
      return devices.find(d => d[fieldName] == value) != null;
    },
    updateDevice(data) {
      axios.patch(`bacnet_master/device/${data.device_uuid}`, data).then(response => {
        const index = this.dataTable.findIndex(d => d.device_uuid == response.device_uuid);
        Object.assign(this.dataTable[index], response);
        this.close();
        this.$toasted.show('update success');
      });
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      (this.editAll = { ...DEVICE_EDIT_ALL_DEFAULT }), (this.dialogMassEdit = true);
      this.editField = fieldName;
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedIndex = this.dataTable.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    onClickRefresh() {
      this.getAllDevices();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if (!e) this.selected = [...this.selected].filter(point => point.network_uuid != items[0].network_uuid);
      else this.selected = [...this.selected].concat(items);
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
